.receive-task-container{
    text-align: right;
    padding: 1rem;
    border-bottom: 1px solid;
}
.receive-task-btn{
    width: 5rem;
    height: 2rem;
    margin: 0 0 0 1rem;
}
.receive-task-input{
    width: 11rem;
    height: 1.65rem;
    text-align: right;
    font-size: 1rem;
}
.all-tasks-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
.each-tasks-container{
    border: 1px solid black;
    padding: 1rem;
    margin: 1rem;

}
.task-title {
      border: 1px solid black;
    padding: 0.5rem;
    margin: 0.5rem;
    text-align: right;
}
.each-new-task, .each-inprogrss-task, .each-ended-task{
      border: 1px solid black;
    padding: 0.5rem;
    margin: 0.5rem;
    text-align: right;
}
.sub-btn-task{
    border: 0.5px solid;
    background-color: #518000;
    padding: 1px;
    cursor: pointer;
}